// ------ HEADER ------
header {
  @include flex-center();
  margin-bottom: 10rem;
  column-gap: 2rem;

  .logo {
    flex: 0 0 20%;

    img {
      width: $one-unit * 20;
    }
  }

  .nav {
    flex: 0 0 50%;
  }

  @include screen-max($sm) {
    margin-bottom: 4rem;
    justify-content: space-between;

    .logo {
      order: 1;
      img {
        width: $one-unit * 10;
      }
    }

    .main-title {
      order: 1;
      font-size: 1.4rem;
    }

    .nav {
      order: 2;
      flex: 0 0 100%;
      margin-top: 2rem;
    }
  }
}
